.circular-cursor-container {
  position: relative;
  overflow: hidden;
}

.circular-cursor {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  pointer-events: none;
  transform: translate(-50%, -50%);
  transition: background-color 0.2s ease;
}

.circular-cursor-container:hover .circular-cursor {
  background-color: transparent;
}

nav {
