}

.ordfull {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 130vh;
  background-color: #16203b;
}

.formorder {
  background-color: #16203b;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 100%;
  height: 100vh;
  border: 2px solid #007BFF;
}

.f3 h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.f1,
.f2 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.f1 div,
.f2 div {
  width: 48%;
}

.formorder ::placeholder {
  color: #fff;
}

.formorder input[type="text"],
input[type="email"],
input[type="tel"] {
  color: #fff;
}

.formorder input[type="text"],
input[type="email"],
input[type="tel"],
select {
  width: 100%;
  background-color: transparent;
  padding: 10px;
  border: none;
  border-bottom: 2px solid #fff;
  border-radius: 5px;
  margin-top: 5px;
  font-size: 16px;
}

.formorder select {
  color: #fff;
}

.formorder option {
  background-color: #16203b;
}

.formorder label {
  font-weight: bold;
  color: #fff;
}

input[type="text"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
select:focus {
  border-color: #007BFF;
  outline: none;
}

.f1 div,
.f2 {
  display: flex;
  flex-direction: column;
}

.f4 {
  margin-bottom: 20px;
}

.f4 span {
  color: red;
  font-size: 14px;
  font-weight: bold;
}

.f3 {
  margin-bottom: 30px;
}

.f3 p {
  font-size: 14px;
  color: #fff;
}

.f3 h1 {
  color: #fff;
}

.f3 label {
  display: inline-block;
  margin-left: 10px;
  font-size: 14px;
}

input[type="radio"] {
  margin-right: 10px;
}

button {
  width: 100%;
  padding: 12px;
  background-color: transparent;
  color: white;
  border-bottom: 2px solid #fff;
  font-size: 16px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
}

button:hover {
  background-color: transparent;
  border-bottom: 2px solid #007BFF;
}

.nametit {
  height: 100vh;
}


.ordered-container {
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 60%;
