.hover-link {
  position: relative;
  display: inline-block;
  color: #007BFF;
  background-color: #fff; 
  border: 2px solid #007BFF;
  border-radius: 20px;
  padding: 10px 20px;
  overflow: hidden;
}

.hover-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%; 
  width: 100%;
  height: 100%;
  background-color: #007BFF; 
  transition: left 0.7s ease; 
  z-index: -1;
}

.hover-link:hover::before {
  left: 0; 
}

.hover-link:hover {
  color: white; 
  z-index: 1; 
  
}
